header{
    height: 100vh;
}

.logo {

    a{
        display: block;
        background-image: url('https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week5/logo-ANSWER.svg');
        background-position: center center;
        width: 151px;
        height: 40px;

        text-indent: 101%;
        overflow: hidden;
        white-space: nowrap;
    }

}

.assignment{
    position: relative;
}

.assignment::before{
    position: absolute;
    left: 0;
    top: 0;
    content: '';
    display: inline-block;
    background-color: #198754;
    width: 4px;
    height: 32px;
}

.user{
    img{
        width: 40px;
        height: 40px;
        border-radius: 100%;
    }
}
  